<template>
  <div class="houseDetail">
    <navigation-bar  @onLeftClick="goBack" pageName="房源详情" :navBarStyle="navStyle" leftImgType="1"/>
    <div class="box" v-if="detail">
        <div class="content-box">
            <van-swipe :autoplay="3000" indicator-color="white" v-if="images && images.length > 0">
                <van-swipe-item v-for="(image, index) in images" :key="index">
                    <img  :src="image.PIC_URL"/>
                </van-swipe-item>
            </van-swipe>
            <div class="default" v-if="images && images.length == 0">
                <img :src="defaultImg" alt="">
            </div>
            <div class="info">
                <div class="title">{{detail.TITLE}}</div>
            </div>
            <div class="base-info">
                <div class="item" v-if="detail.HOUSE_FLAG == '1'">
                    <div class="top">{{detail.PRICE}}元/月</div>
                    <div>租金</div>
                </div>
                <div class="item" v-if="detail.HOUSE_FLAG == '0'">
                    <div class="top">{{detail.PRICE}}万元</div>
                    <div>售价</div>
                </div>
                <div class="item" v-if="detail.CAR_LOCATION_FLAG =='1'">
                    <div class="top">{{detail.ROOM_HALL || "暂无"}}</div>
                    <div>户型</div>
                </div>
                <div class="item" v-if="detail.CAR_LOCATION_FLAG =='0'">
                    <div class="top">{{detail.PARK_NO || "暂无"}}</div>
                    <div>车位号</div>
                </div>
                <div class="item">
                    <div class="top">{{detail.ACREAGE}}平米</div>
                    <div>面积</div>
                </div>
            </div>
            <div class="other-info">
                <div class="item" v-if="detail.CAR_LOCATION_FLAG == 1">
                    <div class="left">
                        <div class="tit">房屋类型：</div>
                        <div class="cont">{{detail.HOUSE_TYPE_NAME || "暂无"}}</div>
                    </div>
                    <div class="right">
                        <div class="tit">装修：</div>
                        <div class="cont">{{detail.DECORATE_TYPE_NAME || "暂无"}}</div>
                    </div>
                </div>
                <div class="item" v-if="detail.CAR_LOCATION_FLAG == 1">
                    <div class="left">
                        <div class="tit">楼层：</div>
                        <div class="cont">{{detail.NOW_FLOOR_CNT && detail.TOTAL_FLOOR_CNT ? detail.NOW_FLOOR_CNT + "/" + detail.TOTAL_FLOOR_CNT : "暂无"}}</div>
                    </div>
                    <div class="right">
                        <div class="tit">朝向：</div>
                        <div class="cont">{{detail.ORIENTATION_TYPE_NAME || "暂无"}}</div>
                    </div>
                </div>
                <div class="item" v-if="detail.CAR_LOCATION_FLAG == 1">
                    <div class="left">
                        <div class="tit">栋号：</div>
                        <div class="cont">{{detail.BUILD_NO || "暂无"}}</div>
                    </div>
                    <div class="right">
                        <div class="tit">单元：</div>
                        <div class="cont">{{detail.UNIT_NO || "暂无"}}</div>
                    </div>
                </div>
                <div class="item">
                    <div class="left">
                        <div class="tit">市区：</div>
                        <div class="cont">{{detail.CITY_NAME || "暂无"}}</div>
                    </div>
                    <div class="right">
                        <div class="tit">区/县：</div>
                        <div class="cont">{{detail.COUNTY_NAME || "暂无"}}</div>
                    </div>
                </div>
                <div class="item">
                    <div class="left">
                        <div class="tit">小区：</div>
                        <div class="cont">{{detail.AREA || "暂无"}}</div>
                    </div>
                </div>
            </div>
            <div class="detail">
                <div>房屋详情</div>
            </div>
            <div class="detail-info">
                {{detail.CONTENT}}
            </div>
        </div>
    </div>  
    <div class="bottom" v-if="detail">
        <div class="rent-name">租赁中心:{{detail.MEDI_NAME}}</div>
        <div class="contact" @click="call">
            <img :src="phoneIcon" alt="">
            <div>电话</div>
        </div>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import { getDetailService } from "../../../services/houseRentalService";
import { store } from "../../../utils/store";
import phoneIcon from "@/assets/images/home/houseRental/phone.png";
import leftIcon from "@/assets/images/common/nav_return_white.png";
import defaultImg from "@/assets/images/home/houseRental/bannerHouse.jpg";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "houseDetail",
  data() {
    return {
      phoneIcon: phoneIcon,
      leftIcon: leftIcon,
      images: [],
      detail: null,
      defaultImg: defaultImg,
      areaName: "",
      cityCode: "",
      cityName: "",
      navStyle: {
        "background": "#00d2ca",
        "color": "#ffffff"
      }
    };
  },
  components: {
    NavigationBar
  },
  created() {
    this.cityCode = this.$route.params.cityCode;
    this.cityName = this.$route.params.cityName;
    this.areaName = this.$route.params.keyWord;
    this.getDetail();
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      if (this.cityCode || this.cityName || this.areaName) {
        store.set("houseCityAndArea", {
          cityName: this.cityName,
          cityCode: this.cityCode,
          areaName: this.areaName
        });
      }
      this.$router.go(-1);
    },

    /**
     * 获取房源详情
     */
    getDetail() {
      var data = {
        HOUSE_CODE: this.$route.params.HouseCode,
        CAR_LOCATION_FLAG: 1 //房源
      };
      getDetailService({
        data: JSON.stringify(data)
      }).then(
        res => {
          this.detail = res;
          this.images = res.PLC_LIST.length > 0 ? res.PLC_LIST : [];
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 拨打电话
     */
    call() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      console.log(isiOS);
      if (isiOS) {
          let that = this;
          setTimeout(
              function(){
                  window.location.href = "tel:" + that.detail.MEDI_PHONE;
              }, 0);
      } else {
        Dialog.confirm({
          title: "拨打电话",
          message: this.detail.MEDI_PHONE
        })
          .then(() => {
              if (this.$deciceType=='phone'){
                  window.location.href = "tel:" + this.detail.MEDI_PHONE;
              }
          })
          .catch(() => {
            // on cancel
            Dialog.close;
          });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.houseDetail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .nav-left {
    height: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 26px;
      height: 26px;
    }
  }
  .box {
    flex: 1;
    overflow: scroll;
    .content-box {
      width: 100%;
      overflow: scroll;
      bottom: 0;
      .default {
        width: 100%;
        height: 240px;
        img {
          width: 100%;
          height: 240px;
        }
      }
    }
    .info {
      .title {
        padding: 12px 12px 0 12px;
        line-height: 20px;
        width: 100%;
        box-sizing: border-box;
        font-weight: 600;
        font-size: 15px;
      }
    }
    .base-info {
      padding: 12px;
      height: 78px;
      width: 100%;
      box-sizing: border-box;
      display: flex;
      justify-content: space-around;
      align-items: center;
      .item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: grey;
        font-size: 15px;
        text-align: center;
        .top {
          color: #ff481f;
          font-weight: 700;
          margin-bottom: 6px;
        }
      }
    }
    .other-info {
      padding: 0 24px;
      width: 100%;
      box-sizing: border-box;
      .item {
        height: 32px;
        line-height: 32px;
        font-size: 14px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .left {
          width: 50%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
        }
        .right {
          width: 50%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
        }
        .tit {
          width: 45%;
          color: grey;
          text-align: right;
        }
        .cont {
          width: 55%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
    .detail {
      margin-top: 20px;
      padding: 8px 0;
      div {
        padding-left: 10px;
        border-left: 5px solid #00d1ca;
        margin-left: 14px;
        font-weight: 600;
        font-size: 14px;
      }
    }
    .detail-info {
      padding: 6px 12px;
      font-size: 14px;
    }
  }
  .bottom {
    height: 44px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid #f2f2f2;
    padding-bottom: 0;
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    .rent-name {
      width: 225px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align: center;
      font-weight: 600;
      font-size: 14px;
    }
    .contact {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 150px;
      height: 44px;
      font-size: 17px;
      background: #00d1ca;
      color: #fff;
      font-weight: 500;
      img {
        width: 26px;
        height: 26px;
        margin-right: 5px;
      }
    }
  }
  /deep/.van-swipe {
    height: 240px;
  }
  /deep/.van-swipe img {
    width: 100%;
    height: 240px;
  }
}
</style>
